import {useRef, useState, useEffect} from 'react';

const BusinessForm = (props) => {
    const nameInput = useRef(null);
    const descriptionInput = useRef(null);
    const urlInput = useRef(null);
    const imageInput = useRef(null);

    const createBusiness = async (e) => {
        e.preventDefault()
        const name = nameInput.current.value;
        const description = descriptionInput.current.value;
        const url = urlInput.current.value;
        const image = imageInput.current.value;
        const body = JSON.stringify({name, description, url, image});
        e.currentTarget.reset();
        console.log(body)
        try {
            const response = await fetch('https://all-in-your-business.herokuapp.com/businesses', {
                method: 'POST',
                headers: {
                    'Content-type': 'application/json'
                },
                body: body
            })
            const data = await response.json();
            props.updateBusinesses([...props.businesses, data])
        } catch(err) {
            console.error(err)
        }

    }

    return (
        <form onSubmit={createBusiness}>
            <input type="text" ref={nameInput} placeholder="Business Name"/>
            <input type="text" ref={descriptionInput} placeholder="Business Description"/>
            <input type="text" ref={urlInput} placeholder="Business URL"/>
            <input type="text" ref={imageInput} placeholder="Add Image"/>
            <input className="submit" type="submit" value="Add Your Business"/>
        </form>
    )
}

export default BusinessForm;