import {useState, useEffect, useRef} from 'react';
import './App.css';
import BusinessForm from './Components/Forms/BusinessForm';//CREATE//
import EditForm from './Components/Forms/EditForm';//EDIT
import RegisterForm from './Components/Forms/RegisterForm';
import LoginForm from './Components/Forms/LoginForm';
import Logout from './Components/Forms/Logout';

function App() {
  const [businesses, setBusinesses] = useState([]);
  const [token, setToken] = useState('');
 
  //READ//
  const fetchBusinesses = async () => {
    try {
      const response = await fetch('https://all-in-your-business.herokuapp.com/businesses');
      const data = await response.json();
      setBusinesses(data)
    } catch(err) {
      console.error(err)
    }
  }

  //UPDATE//
  const updateBusiness = async (id) => {
    try {
      const response = await fetch(`https://all-in-your-business.herokuapp.com/businesses/${id}`, {
        method: 'PUT',
        headers: {
          'Content-type': 'application/json',
          "Authorization": token
        }
      })
      const data = await response.json();
      const filteredBusinesses = businesses.filter(business => business._id !== data._id)
      setBusinesses(filteredBusinesses);
    } catch(err) {
      console.error(err)
    }
  }

  //DELETE//
    const deleteBusiness = async (id) => {
      try {
        const response = await fetch(`https://all-in-your-business.herokuapp.com/businesses/${id}`, {
          method: 'DELETE',
          headers: {
            'Content-type': 'application/json',
            "Authorization": token
          }
        })
        const data = await response.json();
        const filteredBusinesses = businesses.filter(business => business._id !== data._id)
        setBusinesses(filteredBusinesses);
      } catch(error) {
        console.error(error)
      }
    }

  useEffect(() => {
    fetchBusinesses()
    if(window.localStorage.getItem('token')){
      setToken(window.localStorage.getItem('token'))
    }
  }, [])


  return (
    <div className="App">
      <header className="App-header">
      <nav>
      <RegisterForm/>
      <LoginForm/>
      <Logout/>
      </nav>
      <BusinessForm updateBusinesses={setBusinesses} businesses={businesses} />

      <h1>All in your business</h1>
      <p>Find your new favorite small businesses!</p>

      
      <ul>
      {
           businesses.map(business => {
             return (
              <li className="businessName" key ={business._id}>{business.name}<br/>
              <li className="businessDescription">{business.description}</li><br/>
              <img 
               src ={business.image} 
               className="businessImage"/>
               <br/>

               <a className="url" href={business.url}>Check me out!</a><br/>

              <button onClick= {
                 (e) => {
                  deleteBusiness(business._id)
                 }
               }> DELETE {business.name}</button>   
               
               {/* <EditForm updateBusinesses={setBusinesses} businesses= {businesses}/>      */}
               </li>
             )
           })

        }
      </ul>
      </header>
    </div>
  );
}

export default App;
