import {useRef, useState, useEffect} from 'react';

const RegisterForm = (props) => {
    const regNameInput = useRef(null);
    const regPasswordInput = useRef(null);

 const register = async (e) => {
    e.preventDefault()
    const body = JSON.stringify({
      username: regNameInput.current.value,
      password: regPasswordInput.current.value
    })
    e.currentTarget.reset();
    try {
      const response = await fetch('https://all-in-your-business.herokuapp.com/register', {
        method: 'POST',
        headers: {
          'Content-type': 'application/json'
        },
        body
      })
      const data = await response.json();
    } catch (err){
      console.error(err)
    }
  }

  return(
    <form className="registerForm" onSubmit= {register}>
    <input className="registerName" type ="text" placeholder="Create Username" ref={regNameInput} ></input>
    <input className="registerPassword" type="password" placeholder="Create Password" ref={regPasswordInput}></input>
    <input className="registerSubmit"  type="submit" value="Register"></input></form>
  )
}

export default RegisterForm;