 import {useRef, useState, useEffect} from 'react';

 const LoginForm = (props) => {
    const nameInput = useRef(null);
    const passwordInput = useRef(null);
    const [token, setToken] = useState('');

 const login = async (e) => {
    e.preventDefault()
    const body = JSON.stringify({
      username: nameInput.current.value,
      password: passwordInput.current.value
    })
    e.currentTarget.reset();
    try {
      const response = await fetch('https://all-in-your-business.herokuapp.com/login', {
        method: 'POST',
        headers: {
          'Content-type': 'application/json'
        },
        body
      })
      const data = await response.json();
      window.localStorage.setItem('token', `Bearer ${data.token}`);
      setToken(`Bearer ${data.token}`)
    } catch(err) {
      console.error(err)
    }
  }

  return(
    <form className="loginForm" onSubmit={login} >
    <input className="loginPassword" type ="text" placeholder="Username" ref={nameInput} ></input>
    <input className="loginPassword"  type="password" placeholder="Password" ref={passwordInput} ></input>
      <input className="loginSubmit"  type="submit" value="Login" ></input>
    </form>
  )
}

export default LoginForm;